@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");
@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i");
* {
  outline: none;
  box-sizing: border-box; }

ul, li, body {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none; }

body {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  color: #0d3351; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }

.title {
  margin-top: 0;
  font-weight: normal;
  font-size: 36px;
  font-family: "Roboto Slab" ,sans-serif;
  line-height: 1.333;
  text-align: center;
  letter-spacing: 2px;
  color: #0d3351; }

.header {
  background: url("/img/backgrounds/header-bg.png") no-repeat 50% 50%;
  background-size: cover; }
  .header__container {
    padding-bottom: 195px; }
  .header__logo {
    width: 88px;
    height: 88px;
    margin: 3px auto; }
    .header__logo img {
      width: 100%;
      height: 100%; }
  .header__title {
    margin-bottom: 16px; }
  .header__text {
    display: block;
    margin-bottom: 32px;
    font-size: 100px;
    font-family: "Roboto Slab", sans-serif;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: -2.5px;
    color: #0d3351; }
  .header__anchor {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 202px;
    height: 47px;
    font-size: 14px;
    color: #fff;
    background-color: #33c7fd; }
    .header__anchor:hover {
      background-color: #13b0e9; }
    .header__anchor:active {
      background-color: #64d6ff; }

.navigation {
  display: flex;
  align-items: center;
  min-height: 84px; }
  .navigation__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -5px;
    padding: 5px;
    background: none;
    border: none; }
    .navigation__button.navigation__button_active .navigation__burger-element {
      opacity: 0;
      width: 0; }
    .navigation__button.navigation__button_active .navigation__burger:before {
      transform: rotate(45deg);
      top: -1px; }
    .navigation__button.navigation__button_active .navigation__burger:after {
      transform: rotate(-45deg);
      bottom: -1x; }
  .navigation__burger {
    position: relative;
    width: 21px;
    height: 16px; }
    .navigation__burger:before, .navigation__burger:after, .navigation__burger-element {
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #0d3351;
      transform-origin: left center;
      transition: 0.3s; }
    .navigation__burger:before, .navigation__burger:after {
      display: block;
      content: ''; }
    .navigation__burger:before {
      top: 0;
      transform: rotate(0); }
    .navigation__burger:after {
      bottom: 0;
      transform: rotate(0); }
    .navigation__burger-element {
      top: calc(50% - 1px); }
  .navigation__group {
    margin-left: auto;
    display: flex; }
  .navigation__item {
    margin-left: 35px;
    padding-top: 5px; }
    .navigation__item img {
      display: block;
      width: 10px;
      height: 12px;
      margin-bottom: 2px; }
    .navigation__item:not(:last-child) {
      display: flex;
      align-items: center; }
      .navigation__item:not(:last-child):after {
        display: block;
        content: '';
        border-top: 5px solid #0d3351;
        border-bottom: 5px solid rgba(0, 0, 0, 0);
        border-left: 4px solid rgba(0, 0, 0, 0);
        border-right: 4px solid rgba(0, 0, 0, 0);
        transform: translateY(25%); }
    .navigation__item-text {
      margin: 0 8px;
      font-size: 15px; }
  .navigation__phone {
    display: block;
    font-weight: bold;
    font-size: 14px;
    font-family: "Raleway";
    color: #0d3351; }
    .navigation__phone-label {
      display: block;
      font-size: 12px;
      font-family: "Raleway";
      color: #0d3351; }

.main-content {
  background-color: #f3f3f3; }
  .main-content__container {
    padding-top: 107px;
    padding-bottom: 50px; }
  .main-content__title {
    margin-bottom: 7px; }
  .main-content__sort {
    display: block;
    text-align: center;
    letter-spacing: 1px;
    color: #0d3351; }
    .main-content__sort-item {
      position: relative;
      display: inline-flex;
      font-style: italic; }
      .main-content__sort-item:after {
        position: absolute;
        right: -16px;
        top: 5px;
        display: block;
        content: '';
        border-top: 5px solid #0d3351;
        border-bottom: 5px solid rgba(0, 0, 0, 0);
        border-left: 4px solid rgba(0, 0, 0, 0);
        border-right: 4px solid rgba(0, 0, 0, 0);
        transform: translateY(25%); }

.catalog {
  padding: 42px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: -15px;
  margin-right: -15px;
  display: block; }
  .catalog:after {
    display: table;
    content: '';
    clear: both; }
  .catalog__item {
    float: left;
    width: calc(100% / 12 * 6 - 15px * 2);
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 20px; }

.cruise {
  background-color: #fff; }
  .cruise__image img {
    display: block; }
  .cruise__description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 24px 38px 37px; }
  .cruise__reference {
    display: block;
    margin-bottom: 10px;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    letter-spacing: 0.8px;
    color: #00a2dd; }
  .cruise__route, .cruise__time {
    margin-top: 0;
    margin-bottom: 10px; }
    .cruise__route-key, .cruise__time-key {
      font-weight: bold; }
  .cruise__time {
    margin-bottom: 15px; }
  .cruise__price-actual {
    font-size: 30px;
    font-family: "Roboto Slab", sans-serif;
    letter-spacing: 1px; }
  .cruise__price-old {
    margin-left: 30px;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    color: rgba(13, 51, 81, 0.549);
    text-decoration: line-through; }
